import { Container, Spinner } from 'react-bootstrap';
import '../Home/Home.css';


function Loader(){


    return (
        <>
            <Container fluid className='px-0'>
                <div className='flexCenter align-items-center bg-primary' style={{height: '100vh'}}>
                    <div className='d-flex' style={{width: '80%', height: '80vh', color: 'white'}}>
                        <div className='leftSection'>
                            <div className='black d-flex justify-content-end align-items-center'>
                                <Spinner style={{width: '5rem', height: '5rem'}} animation="border" variant="primary" />
                            </div>
                        </div>
                        <div className='rightSection'>
                            <div className='black'>
                            </div>
                        </div>
                    </div>
                </div>

            </Container>
        </>
    )
}

export default Loader;